@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.mkdf-comment-holder {
    @include mkdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .mkdf-comment-holder-inner {
        padding: 36px 0 27px;
    }
	
	.mkdf-comment {
		
		&.mkdf-pingback-comment {
			
			.mkdf-comment-text {
				padding: 0;
			}
		}
	}

    .mkdf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include mkdfRelativeHolderLayout();
            margin: 30px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
			position: relative;
            padding: 0 0 0 114px;
			.mkdf-comment{
				background-color: rgba($third-main-color, 0.1);
				border: 1px solid rgba($third-main-color, 0.8);
			}
			&:before{
				content: "\e043";
				font-family: "dripicons-v2" !important;
				position: absolute;
				left: 56px;
				top: 26px;
				font-size: 36px;
				color: $first-main-color;


				@include ipad-portrait{
					left: 0;
				}

				@include phone-landscape{
					display: none;
				}


			}
        }
		.mkdf-comment{
			background-color: rgba($fourth-main-color, 0.1);
			border: 1px solid rgba($fourth-main-color, 0.8);
			border-radius: 10px;
			padding: 13px 20px;
			box-sizing: border-box;
		}
    }

    .mkdf-comments-title {
        margin: 0 0 15px;

        > * {
            margin: 0;
        }
    }

    .mkdf-comment-image {
        position: relative;
        display: block;
        top: 10px;
        left: 0;
        width: 90px;
        height: 80px;
        float: left;

        img {
            display: block;
            border-radius: 10px;
        }
    }

    .mkdf-comment-text {
        padding: 10px 0 8px 117px;
        min-height: 82px;

        .mkdf-comment-info {
            @include mkdfRelativeHolderLayout();
            margin: 0 0 10px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
			position: relative;
            margin-right: 10px;
			font-family: $default-heading-font;
			z-index: 50;
			padding: 0 3px;
            &:hover {
                color: $default-heading-color;
				&:after{
					width: 100%;
				}
            }
			&:after{
				content: '';
				position: absolute;
				bottom: 2px;
				left: 0;
				width: 0;
				height: 9px;
				background-color: #d7f5fa;
				z-index: -1;
				@include mkdfTransition(width .3s ease);
			}
        }

        .mkdf-comment-name {
            float: left;
            margin: 0;
			max-width: 80%;
        }

        .mkdf-comment-date {
            float: left;
			color: $first-main-color;
			margin: 0 0 0 12px;
			line-height: 20px;

			@include phone-landscape{
				margin: 0;
			}

        }

        .mkdf-text-holder p {
            margin: 0;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
			padding: 0 3px;
            margin-left: 10px;
            font-size: 14px;
            color: $first-main-color;
			text-transform: capitalize;
			z-index: 50;
			&:after{
				content: '';
				position: absolute;
				bottom: 2px;
				left: 0;
				width: 0;
				height: 9px;
				background-color: #d7f5fa;
				z-index: -1;
				@include mkdfTransition(width .3s ease);
			}
			&:hover {
				&:after{
					width: 100%;
				}
			}

        }
	
	    ul, ol {
		    
		    li {
			    display: list-item;
		    }
	    }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
		.mkdf-text-holder{
			margin: 0 0 8px;
		}
    }
}

.mkdf-comment-form {
    clear: both;
    box-sizing: border-box;
    background-color: $second-main-color;
    border-radius: 10px;
    margin: 53px 0 0;
    padding: 0;
    
    .mkdf-comment-form-inner {
        padding: 45px 45px 54px;

		.comment-respond{
			.comment-form-cookies-consent{
				color: #FFF;
				margin: 0 0 20px;

				input{
					vertical-align: text-top;
				}

				label{
					top: -1px;
					position: relative;
				}
			}
		}

		@include phone-landscape{
			padding: 45px 20px 54px;
		}
    }
    
    .comment-reply-title {
        color: #fff;
        margin: 0 0 25px;
    }
    
    .logged-in-as {
        margin: 0 0 8px;
    }

	#respond textarea{
		height: 135px;
	}
}

@include ipad-portrait {

    .mkdf-comment-holder {
	    
        .mkdf-comment-list {
	        
            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {
	
    .mkdf-comment-holder {
	    
        .mkdf-comment-list {
	        
            .children {
                padding: 0;
            }
        }

        .mkdf-comment-text {
	        
            .mkdf-comment-date,
            .mkdf-comment-name {
                float: none;
            }
	        
            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {
	
    .mkdf-comment-holder {

        .mkdf-comment-image {
            float: none;
        }

        .mkdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.mkdf-style-form textarea,
#respond input[type='text'],
#respond input[type='email'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
	@extend %input-style;
}

.post-password-form input[type='password'] {
	margin-top: 10px;
}

.wpcf7-form-control {
	&.wpcf7-text,
	&.wpcf7-number,
	&.wpcf7-date,
	&.wpcf7-textarea,
	&.wpcf7-select,
	&.wpcf7-quiz {
		@extend %input-style;
        font-family: inherit;
        font-size: 16px;
        font-weight: 300;
        font-style: normal;
        color: #fff;
        border: 1px solid #72cbc4;
        border-radius: 10px;
        background-color: #7dd1ca;
        width: 100%;
        padding: 19px 29px;
        line-height: 20px;
        margin: 0 0 20px;
        outline: 0;
        -webkit-appearance: none;
        box-sizing: border-box;
        @include mkdfTransition(border-color 0.2s ease-in-out);

        &:focus {
            border-color: rgba($fourth-main-color, 0.5);
        }

        @include mkdfPlaceholder {
            color: inherit;
        }

		
		footer & {
			margin: 0;
		}
	}
	&.wpcf7-checkbox{
		 .wpcf7-list-item{
			 display: block;
			 margin: 4px 0 8px;
			 &.last{
				 margin-bottom: 24px;
			 }
		 }
	 }
	&.wpcf7-textarea{
		max-width: 100%;
	}
}

div.wpcf7 .mkdf-centered-cf7-btn{
	text-align: center;

	.ajax-loader{
		display: none !important;
	}
}

.wpcf7-submit.mkdf-cf7-submit-with-icon + .ajax-loader{
	display: none !important;
}

.mkdf-btn.mkdf-btn-solid.mkdf-btn-cf7-huge{
	width: 100%;

	.wpcf7-submit.mkdf-cf7-submit-with-icon{
		width: calc(100% - 54px);
		padding-left: 0;
		padding-right: 0;
	}
}

.mkdf-btn-cf7-medium .wpcf7-form-control.wpcf7-submit.mkdf-cf7-submit-with-icon{
	@include phone-portrait{
		padding-left: 25px;
		padding-right: 25px;
	}
}

#submit_comment,
.post-password-form input[type='submit']{
	@include mkdfButtonDefaultStyle();
	@include mkdfButtonMediumParams();
	@include mkdfButtonSolidColor();
	cursor: pointer;
	
	&:hover {
		@include mkdfButtonSolidHoverColor();
	}

}

input.wpcf7-form-control.wpcf7-submit {
	@include mkdfButtonDefaultStyle();
	@include mkdfButtonMediumParams();
	@include mkdfButtonSolidColor();
	cursor: pointer;

	&:not(.mkdf-cf7-submit-with-icon):hover {
		@include mkdfButtonSolidHoverColor();
	}

}

input.wpcf7-form-control.wpcf7-submit.mkdf-newsletter-submit{
	font-family: "dripicons-v2";
}


p.form-submit,
.wpcf7-form p {
	margin: 0;
}
p.form-submit{
  margin-top: 10px;
}

#respond textarea,
.mkdf-style-form textarea,
.wpcf7-form textarea {
	display: block;
	padding: 16px 18px;
}

.mkdf-two-columns-form-without-space {
	
	.wpcf7-form .ajax-loader {
		display: none;
	}
}

span.wpcf7-form-control-wrap.mkdf-select:after {
  font-family: FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  content: '\f107';
  right: 20px;
  top: 0px;
  padding: 0 0 2px;
  position: absolute;
  pointer-events: none;
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.mkdf-owl-slider {
	visibility: hidden;
	
    .owl-stage-outer {
        z-index: 1;
    }
	
	&.mkdf-slider-is-moving {
		
		.owl-item {
			
			.mkdf-block-drag-link {
				z-index: -1 !important;
			}
		}
	}
    
    &.mkdf-slider-has-both-nav {
        
        .owl-nav {
            
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include mkdfTransform(translateY($pos));
            }
        }
    }
	
	.owl-nav {
		
		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			z-index: 3;
			color: $default-heading-color;
			@include mkdfTransform(translateY(-50%));
			@include mkdfTransition(color .2s ease-out);
			
			&:hover {
				color: $first-main-color;
			}
			
			> span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-size: 46px;
				line-height: 1;
				
				&:before {
					display: block;
					line-height: inherit;
				}
			}

			@include keyframes(mkdfrotate){
				to{
					transform: rotate(360deg);
				}
			}

			.mkdf-arrow-stack-outer{
				display: inline-block;
				padding: 3px;
				border-radius: 50%;

				.mkdf-arrow-stack{
					position: relative;
					display: block;
					width: 60px;
					height: 60px;
					line-height: 64px;
					font-size: 28px;
					text-align: center;
					color: #fff;
					background-color: #FFF;
					border-radius: 50%;
					overflow: hidden;
					@include mkdfTransition(color .3s ease,background-color .3s ease,border-color .3s ease);
					.mkdf-prev-icon{
						color: $first-main-color;
						@include mkdfTransition(color .2s ease-out);

						&:hover{
							color: $second-main-color;
						}
					}
					.mkdf-next-icon{
						color: $second-main-color;
						@include mkdfTransition(color .2s ease-out);

						&:hover{
							color: $first-main-color;
						}
					}
				}

				&:after {
					content: "";
				    border: 1px dashed #fff;
				    position: absolute;
				    width: 100%;
				    height: 100%;
				    border-radius: 100%;
				    top: -1px;
				    left: -1px;
				}

				&:hover {
					&:after { 
						@include mkdfAnimation(mkdfrotate 7s linear infinite);
					}
				}
			}

		}
		
		.owl-prev {
			left: 10px;
		}
		
		.owl-next {
			right: 10px;
		}
	}
	
	.owl-dots {
		text-align: center;
		margin: 63px 0 0;
		
		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			
			span {
				display: inline-block;
				vertical-align: middle;
				width: 12px;
				height: 12px;
				margin: 0 3px;
				border-radius: 3em;
				background-color: $default-heading-color;
				box-sizing: border-box;
				@include mkdfTransition(background-color .2s ease-out, border .2s ease-out);
			}
			
			&:hover,
			&.active {
				
				span {
					background-color: $first-main-color;
				}
			}
		}
	}
	
	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}


    + .mkdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .mkdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            img {
                border: 2px solid transparent;
                @include mkdfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.mkdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .mkdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }


    }

}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {

    .mkdf-wrapper {
        padding: 0 !important;
    }
    
    .mkdf-content {
        height: 100vh;
    }
	
	&.admin-bar {
		
		.mkdf-content {
			height: calc(100vh - 32px);
		}
	}

    .mkdf-content-inner {
	    @include mkdfTableLayout();
    }

    .mkdf-page-not-found {
	    @include mkdfTableCellLayout();
        padding: 0 20%;
	    text-align: center;

        .mkdf-404-title {
            margin: 0;
        }
	
	    .mkdf-404-subtitle {
            margin: 35px 0 0;
        }
	    
	    .mkdf-404-text {
		    margin: 10px 0 0;
	    }

		.mkdf-404-form{
			&.mkdf-404-form-light{
				.input-holder{
					border-color: #fff;

					input,
					button{
						color: #fff;
					}

					input.search-field {

						@include mkdfPlaceholder {
							color: #fff;
						}
					}

					button:hover{
						color: rgba(255,255,255,0.8);
					}
				}
			}

			.input-holder {
				width: 45%;
				min-width: 250px;
				border: 1px solid $default-border-color;
                border-radius: 10px;
				margin: 30px auto 0;

				input,
				button {
					background-color: transparent;
					border: 0;
					border-radius: 0;
					box-sizing: border-box;
					color: $default-form-color;
					display: inline-block;
					float: left;
					font-family: $default-text-font;
					font-size: 14px;
					font-style: italic;
					font-weight: inherit;
					height: 48px;
					line-height: 46px;
					outline: 0;
					position: relative;
					vertical-align: top;
					-webkit-appearance: none;

					@include mkdfTransition(color .2s ease-in-out);
				}

				input.search-field {
					width: calc(100% - 48px);
					padding: 10px 0 10px 21px;

					@include mkdfPlaceholder {
						color: $default-form-color;
					}

				}

				button {
					background: transparent;
					color: $default-heading-color;
					cursor: pointer;
					font-size: 17px;
					line-height: 1em;
					width: 48px;

					@include mkdfTransition(color .2s ease-in-out);

					&:hover {
						color: #000;
					}

					span {
						display: block;
						left: 0;
						line-height: 48px;
						position: absolute;
						top: 0;
						width: 100%;

						&:before {
							display: block;
							line-height: inherit;
						}
					}
				}
			}
		}
    }


	@include ipad-landscape {

		.mkdf-content {
			height: auto;
			min-height: 100vh;

			.mkdf-content-inner {
				min-height: 100vh;
			}
		}

		&.admin-bar {

			.mkdf-content {
				height: auto;
				min-height: calc(100vh - 32px);
			}
		}
	}

	@include ipad-portrait {

		.mkdf-page-not-found {
			padding: 70px 30px 20px;
		}
	}
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#mkdf-back-to-top {
    position: fixed;
    width: 54px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;
        @include mkdfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include mkdfTransition(opacity 0.3s ease);
    }
	.mkdf-icon-stack-outer{
		display: inline-block;
		position: relative;
		padding: 6px;
		background-color: rgba($first-main-color, 0.1);
		border-radius: 50%;

		&:after {
			content: "";
			border: 1px dashed $first-main-color;
			border-radius: 100%;
			position:absolute;
			width: calc(100% - 4px);
		    height: calc(100% - 4px);
		    border-radius: 100%;
		    top: 1px;
		    left: 1px;
		}

		.mkdf-icon-stack {
			position: relative;
			display: block;
			width: 39px;
			height: 39px;
			line-height: 40px;
			font-size: 15px;
			text-align: center;
			color: #fff;
			background-color: $first-main-color;
			border: 1px solid $first-main-color;
			border-radius: 50%;
			overflow: hidden;
			@include mkdfDefaultTransition(color, background-color, border-color);

			i{
				position: relative;
				display: inline-block;
			}

			.mkdf-icon-stack-inner{
				position: relative;
				@include mkdfTransform(rotate(0deg) translate(0px,1px) scale(1) rotateX(0));
				@include mkdfTransition(transform .8s cubic-bezier(.65,-.88,.93,1.18));
			}
		}
	}

    span span {
        display: block;
        line-height: inherit;
    }
	.mkdf-back-to-top-inner{
		position: absolute;
		top: 0;
		left: 0;
		width: 54px;
		height: 54px;
		line-height: 54px;
		text-align: center;
		color: #FFF;
		background: 0 0;
		border: none;
		opacity: 0;
		@include mkdfTransform(translate3d(0,75%,0) rotateX(-90deg));
		@include mkdfTransition(all .5s cubic-bezier(.65,-.88,.93,1.18) 0s);
	}
	&:hover{

		.mkdf-icon-stack-outer{ 
			&:after { 
				@include mkdfAnimation(mkdfrotate 7s linear infinite);
			}
		}

		.mkdf-back-to-top-inner{
			opacity: 1;
			@include mkdfTransform( rotate(-45deg) translate(0,1px) scale(1) rotateX(0));
			@include mkdfTransition(all .5s cubic-bezier(.65,-.88,.93,1.18) .25s);
		}
		.mkdf-icon-stack-outer{
			.mkdf-icon-stack{
				.mkdf-icon-stack-inner{
					@include mkdfTransform(rotate(-45deg) translate3d(33px,-29px,0) scale(1.5) rotateX(90deg));
				}
			}
		}
	}
}
/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.mkdf-self-hosted-video-holder {
	
    .mkdf-video-wrap {
	
	    .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}

.mkdf-background-image-chld{
	@media (max-width:$laptop-landscape-large) and (min-width:$laptop-landscape-medium) {
		background-size: 100% 100%;
	}
}

