/* ==========================================================================
   Default WordPress widgets styles - begin
   ========================================================================== */

.widget {
    background-color: #FFF;
    border-radius: 10px;
    margin: 0 0 60px;

    ul{
        padding: 33px 36px 36px;
        ul{
            padding: 0;
			margin-bottom: -15px;
        }
    }

    .mkdf-widget-title-holder{
        background-color: #62c7b9;
        padding: 22px 36px;
        margin-bottom: 0;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        .mkdf-widget-title{
            color: #FFF;
        }
    }

    // tag cloud
    &.widget_tag_cloud {

        a {
            background: $widget-background-color;
        }
    }
    &:nth-child(3n + 2){
        .mkdf-widget-title-holder{
            background-color: $first-main-color;
        }
    }
    &:nth-child(3n + 3){
        .mkdf-widget-title-holder{
            background-color: $first-main-color;
        }
    }
    .tagcloud,
    .textwidget,
    .searchform,
    #calendar_wrap{
        padding: 33px 36px 36px;
    }
    &.widget_rss{
        .rsswidget{
            color: #FFF;
        }
    }
    &.widget_products{
        ul{
            padding: 43px 36px 33px;
        }
    }
    .wpcf7{
        padding: 35px 30px 36px;
        background-color: $second-main-color;
        border-radius: 10px;
    }
    .product_list_widget{
        padding: 27px 36px 30px;
        li{
            padding: 0 0 14px;
        }
    }
    &.widget_product_categories{
        ul{
            li{
                padding: 0 0 11px 15px;
            }
        }
        > ul{
            > li{
                padding-left: 0;
            }
        }
    }
}