/* ==========================================================================
   Blog List widget style - begin
   ========================================================================== */
aside.mkdf-sidebar {
    .widget.mkdf-blog-list-widget {
        margin: 0 0 20px;

        > h4 {
            margin: 0 0 15px;
        }

        footer .mkdf-footer-top &,
        .mkdf-top-bar & {

            > h2 {
                margin: 0 0 8px;
            }
        }
        .mkdf-blog-list-holder {
            &.mkdf-bl-simple {
                .mkdf-post-image {
                    width: 93px;
                    vertical-align: middle;
                }
                .mkdf-post-info-date {
                    height: 17px;
                    padding: 0 8px 0px;
                    margin: 0 0 9px;
                    a {
                        font-size: 11px;
                        line-height: 15px;
                        vertical-align: top;
                    }
                }
                .mkdf-bli-content {
                    &:not(:first-child) {
                        padding-left: 17px;
                    }
                    p.mkdf-post-title{
                        a{
                            color: $default-text-color;

                            &:hover{
                                color: $first-main-color;
                            }

                        }
                    }
                }
            }
        }
        .mkdf-widget-title-holder {
            margin-bottom: 22px;
        }
    }
}
/* ==========================================================================
   Blog List widget style - end
   ========================================================================== */