/* ==========================================================================
   Author Info widget style - begin
   ========================================================================== */

aside.mkdf-sidebar {
	.mkdf-author-info-widget {
		padding: 30px 0;
	}
}

.widget {
	&.mkdf-author-info-widget {
		border: 1px solid $first-main-color;
		padding: 30px 0;
		text-align: center;
		color: inherit;
		line-height: inherit;
		font-size: inherit;
		font-style: inherit;
		font-weight: inherit;
		letter-spacing: inherit;
		.mkdf-aiw-inner {
			ul {
				li {
					display: inline-block;
					margin: 0 15px 0 0;
					padding: 0;
					&:last-child {
						margin-right: 0;
					}
				}
				a {
					font-size: 15px;
				}
			}
		}
		.mkdf-aiw-title {
			margin: 0;
			line-height: 1.1em;
			letter-spacing: 0.1em;
		}
		.mkdf-aiw-image {
			display: inline-block;
			img {
				border-radius: 100%;
			}
		}
		.mkdf-aiw-text {
			display: block;
			font-size: 16px;
			text-transform: uppercase;
			color: $first-main-color;
			font-weight: 600;
			letter-spacing: 0.1em;
			p {
				margin: 0 0 14px 0;
			}
		}
	}
}

/* ==========================================================================
   Author Info widget style - end
   ========================================================================== */