// widgets spacing
$widget-bottom-margin: 35px;
$widget-bottom-padding: 7px;
$widget-inner-padding: 10px;
$widget-title-margin: 12px;

// widget color
$widget-background-color: transparent;

// list items dimensions
$widget-list-item-height: 17px;
$widget-list-item-padding: 10px;

// list items color
$widget-list-item-background-color: $second-main-color;

// list items border
$widget-list-item-border-color: #72cbc4;
$widget-list-item-border-width: 1px;
$widget-list-item-border-style: solid;

// text
$widget-text-color: $default-text-color;
$widget-heading-color: $default-heading-color;

// links
$widget-link-color: $default-text-color;
$widget-link-hover-color: $first-main-color;

// specific widgets
$widget-search-form-height: 48px;
$widget-tag-cloud-height: 25px;

aside.mkdf-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {
        color: $widget-text-color;

        &:last-child {
            margin: 0 !important;
        }
    }
}

.wpb_widgetised_column {
    position: relative;

    .widget {
        &:last-child {
            margin: 0 !important;
        }
    }
}

aside.mkdf-sidebar,
.wpb_widgetised_column {
    @import '../widget-types/default-wordpress-widgets';
    @import '../widget-types/default-wordpress-widgets-flat';
}

.mkdf-sidebar-holder{
    &.mkdf-sidebar-boxed{
        aside.mkdf-sidebar{
            @import '../widget-types/default-wordpress-widgets';
            @import '../widget-types/default-wordpress-widgets-boxed';
        }
    }
}
